@import url(http://weloveiconfonts.com/api/?family=fontawesome);
body {
  background: #EEEE;
  font-family: verdana;
  background-size: cover;
  min-height: 800px; }
  @media (max-width: 992px) {
    body {
      min-height: 700px; } }

.header img {
  background: transparent;
  display: block;
  margin: 0 auto;
  max-width: 30%;
  height: auto;
  width: auto\9; }

.withdrawnShop {
  color: red; }

.activeShop {
  color: green; }

.btn_home {
  background-color: transparent;
  border: none;
  color: black;
  text-align: center;
  margin: 4px 2px;
  cursor: pointer;
  font-family: verdana;
  font-size: 16px;
  display: inline; }

#eye {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  position: relative; }

.Register {
  text-align: left;
  padding: 1em;
  margin: 1em;
  border: 2px solid #d3d3d3;
  border-radius: .5em;
  vertical-align: middle;
  margin-left: auto;
  margin-right: auto;
  width: 700px;
  margin-top: 50px; }

.Register Input:hover,
.Register Input:focus {
  outline: none;
  box-shadow: none;
  border-color: #ccc; }

.Register Input {
  height: auto;
  border-radius: 2rem;
  width: 300%; }

[class*="fontawesome-"]:before {
  font-family: 'fontawesome', verdana; }

.marker {
  color: white; }

.search {
  position: absolute;
  top: 35%;
  display: block;
  margin: auto;
  font-family: verdana; }

.search_input {
  width: 100%;
  padding: 5px 10px;
  margin-bottom: auto;
  position: relative;
  border-radius: 4px; }

.search_input button {
  position: absolute;
  right: 4%;
  top: 20%; }

.icon {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none; }

.search_btn {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none; }

.homepage {
  top: 10px;
  left: 10px;
  background-repeat: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  display: inline;
  font-family: verdana;
  margin: 4px 2px;
  padding: 5px;
  text-align: center;
  font-size: 16px;
  border-radius: 12px; }

.range {
  -webkit-appearance: none;
  background: #a70404;
  outline: none;
  height: 6px;
  width: 100px;
  border-radius: 5px; }
  .range::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: black; }

.tick {
  color: #1f0202;
  border-radius: 2px;
  padding: 3px 7px;
  margin: 0px 10px;
  position: relative; }

.addform {
  position: static; }

.Search {
  text-align: left;
  padding: 1em;
  margin: 1em;
  border: 2px solid #d3d3d3;
  border-radius: .5em;
  vertical-align: middle;
  margin-left: auto;
  margin-right: auto;
  width: 1000px;
  margin-top: 20px; }

.modal_big {
  width: 100%;
  height: 80VH;
  max-height: 500px; }

.map_class {
  position: relative;
  padding: 3px; }

.nav-link {
  cursor: pointer; }

.row_pointer {
  cursor: pointer; }

.btn {
  width: auto;
  color: black;
  background-color: snow; }

.btn:hover {
  color: black;
  background-color: transparent; }

.btn:active {
  color: red;
  background-color: transparent; }

.dropdown-item {
  color: black;
  background-color: white; }

.dropdown-item:focus {
  color: blue;
  background-color: white; }

.navbar-brand {
  cursor: pointer;
  font-size: 14px; }

.dropdown-menu {
  color: #000;
  background-color: white; }

.front-img {
  background: url(/public/images/option5.jpg) no-repeat;
  background-size: cover;
